.header {
  display: flex;
  justify-content: space-between;
}

body {
  min-height: 100vh;
}

h3 {
  margin: 0;
}

.error,
.error:focus,
.error:active {
  border: red 1px solid;
  outline: red;
}

.error-message {
  color: red;
}

.is-done {
  opacity: 0.5;
  text-decoration: line-through;
}

.tasks_container {
  padding: 15px 0;
  margin: 0;
}

.tasks_container li {
  list-style: none;
}

.btns_container {
  margin: 0 0 10px 0;
}

.mb {
  margin-bottom: 10px;
}

.height_login {
  height: calc(100vh - 60px);
}
